<template>
  <div class="importCustomerInfoModal">
    <!--<Button type="primary" @click="showModal"> New Tag</Button>-->
    <a-modal
      title="数据导入-上传数据文件"
      :visible="visible"
      @ok="handleOk"
      :confirmLoading="confirmLoading"
      @cancel="handleCancel"
      :footer="null"
      width="1000px"
    >
      <a-steps :current="current">
        <a-step v-for="item in steps" :key="item.title" :title="item.title" />
      </a-steps>
      <div class="steps-content">
        <h5 class="subTitle">{{steps[current].subTitle}}</h5>
        <a-divider/>
        <upload-data-file v-if="current==0" ref="childUploadDataFile"></upload-data-file>
        <import-settings v-if="current==1" ref="childImportSettings" :getDataSource="childUploadDataFileData"></import-settings>
        <select-import-fields v-if="current==2" ref="childSelectImportFields" :getDataSource="childUploadDataFileData"></select-import-fields>
      </div>
      <div class="steps-action">
        <a-button
          v-if="current>0"
          style="margin-left: 8px"
          @click="prev"
        >
          上一步
        </a-button>
        <a-button
          v-if="current < steps.length - 1"
          type="primary" @click="next"
        >
          下一步
        </a-button>
        <a-button
          v-if="current == steps.length - 1"
          type="primary"
          @click="$notification['warning']({message: 'Processing complete!'})"
        >
          完成
        </a-button>
      </div>
    </a-modal>
  </div>
</template>
<script>
  import AModal from 'ant-design-vue/lib/modal'
  import ASteps from 'ant-design-vue/lib/steps'
  import ADivider from 'ant-design-vue/lib/divider'
  import AButton from 'ant-design-vue/lib/button'
  import UploadDataFile from './UploadDataFile'
  import ImportSettings from './ImportSettings'
  import SelectImportFields from './SelectImportFields'
  const AStep=ASteps.Step
  export default {
    components: {
      AModal,
      ASteps,
      AStep,
      ADivider,
      AButton,
      UploadDataFile,
      ImportSettings,
      SelectImportFields,
    },
    data() {
      return {
        ModalText: 'Content of the modal',
        visible: false,
        confirmLoading: false,
        current: 0,
        steps: [{
          title: '上传数据文件',
          subTitle: '上传数据文件',
          content: '上传数据文件',
        }, {
          title: '导入设置',
          subTitle: '导入设置',
          content: '导入设置',
        }, {
          title: '选择导入字段',
          subTitle: '选择导入字段',
          content: '选择导入字段',
        }, {
          title: '设置字段默认值',
          subTitle: '设置字段默认值',
          content: '设置字段默认值',
        }, {
          title: '数据导入完成',
          subTitle: '数据导入完成',
          content: '数据导入完成',
        }],
        childUploadDataFileData:[],
      }
    },
    methods: {
      next() {
        this.current++
        this.childUploadDataFileData=this.$refs.childUploadDataFile.dataSource
      },
      prev() {
        this.current--
      },
      showImportCustomerInfoModal() {
//        debugger;
        this.visible = true
      },
      handleOk(e) {
        this.ModalText = 'The modal will be closed after two seconds';
        this.confirmLoading = true;
        setTimeout(() => {
          this.visible = false;
          this.confirmLoading = false;
        }, 2000);
      },
      handleCancel(e) {
        this.visible = false
      },

    }
  }
</script>
<style scoped>
  .steps-content {
    margin-top: 16px;
    border: 1px dashed #e9e9e9;
    border-radius: 6px;
    /*background-color: #fafafa;*/
    min-height: 200px;
    /*text-align: center;*/
    padding: 16px 24px;
  }

  .steps-action {
    margin-top: 24px;
    text-align: right;
  }
</style>



