<template>
  <div class="searchCustomerInfoModal">
    <!--<Button type="primary" @click="showModal"> New Tag</Button>-->
    <a-modal title="查询" :visible="visible" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel" width="907px">
      <query id="customer-query" :structure="structure" ref="dialogtreeQuery"></query>
    </a-modal>
  </div>
</template>

<script>
import AModal from "ant-design-vue/lib/modal";
import query from "@/components/Query/Query"
import moment from 'moment'
// import { constants } from 'http2';
/*
   * EditableCell Code https://github.com/vueComponent/ant-design-vue/blob/master/components/table/demo/EditableCell.vue
   */
export default {
  props:{
    querySuccess:Boolean
  },
  components: {
    AModal,
    query
  },
  data() {
    return {
      accounts_dictionary: this.$localstorage.getLocalStorage("accounts_dictionary") || null,
      ModalText: "Content of the modal",
      visible: false,
      confirmLoading:false,
      //查询条件的结构
    };
  },
  mounted(){
  },
  computed:{
    structure(){
      return {
        fields: [
          {
            id: 1,
            field: "owner",
            text:"姓名",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 2,
            field: "mobile",
            text:"手机",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 3,
            field: "plate_no",
            text:"车牌",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 4,
            field: "vin",
            text:"VIN码",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 5,
            field: "model",
            text:"品牌",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 6,
            field: "register_date",
            text:"注册日期",
            type: "date",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 7,
            field: "date_create",
            text:"创建时间",
            type: "datetime",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 8,
            field: "company",
            text:"保险公司",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 9,
            field: "business_expiration_date",
            text:"商业到期",
            type: "datetime",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 10,
            field: "preset_time",
            text:"预约时间",
            type: "datetime",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 11,
            field: "status",
            text:"状态",
            type: "select",
            options:this.accounts_dictionary?this.accounts_dictionary.status:{},
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 12,
            field: "batch",
            text:"批次",
            type: "select",
            options:this.accounts_dictionary?this.accounts_dictionary.batch:{},
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 13,
            field: "intention",
            text:"意向程度",
            type: "select",
            options:this.accounts_dictionary?this.accounts_dictionary.intention:{},
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 14,
            field: "type",
            text:"类型",
            type: "select",
            options:this.accounts_dictionary?this.accounts_dictionary.type:{},
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 15,
            field: "remark",
            text:"备注",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 16,
            field: "user_attach",
            text:"归属于",
            type: "input",
            options:[],
            isDuration:false,//值是否为时长（单位为分）
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
        ],
        stringConditions: [
          {
            id: 1,
            condition: "=",
            text:"等于",
            type: "input"
          },
          {
            id: 2,
            condition: "like_contain",
            text:"包含",
            type: "input"
          }
        ],
        numberConditions: [
          {
            id: 1,
            condition: "=",
            text:"等于",
            type: "input"
          },
          {
            id: 2,
            condition: "!=",
            text:"不等于",
            type: "input"
          },
          {
            id: 3,
            condition: ">",
            text:"大于",
            type: "input"
          },
          {
            id: 4,
            condition: ">=",
            text:"大于等于",
            type: "input"
          },
          {
            id: 5,
            condition: "<",
            text:"小于",
            type: "input"
          },
          {
            id: 6,
            condition: "<=",
            text:"小于等于",
            type: "input"
          }
        ],
        connectionWay: [
          {
            connection: "与"
          },
          {
            connection: "或"
          }
        ]
      }
    }
  },
  methods: {
    moment,
    showSearchCustomerInfoModal() {
      this.visible = true;
    },
    handleOk(e) {
      let  _this = this;
      let childElm = this.$refs.dialogtreeQuery
      const SCResult = childElm.searchConditionResult
      childElm.dataSource.forEach(function(item) {
        if (item.fieldSelected) {
          if(item.valueType==="input"||item.valueType==="select"){
            SCResult.push({
                // key: item.key || {},
                field: item.fieldSelected || "",
                condition: item.conditionSelected || "",
                value: item.value || "",
                link: item.link || ""
            })
          }else if(item.valueType==="date"||item.valueType==="datetime"){
            if(item.value){
              const time = item.value.split("~")
              SCResult.push({
                  // key: item.key || {},
                  field: item.fieldSelected || "",
                  condition: ">=",
                  value: time[0]|| "",
                  link: item.link || ""
              })
              SCResult.push({
                  // key: item.key || {},
                  field: item.fieldSelected || "",
                  condition: "<=",
                  value: time[1]|| "",
                  link: item.link || ""
              })
            }
          }else{
            _this.$notification['warning']({
                  message: '类型有误',
                })
          }
        }
      });
      _this.$emit('searchHandle',SCResult)
      childElm.searchConditionResult = []
      // .then(result => {
      //   // _this.confirmLoading = false;
      // }).catch(err => {
      //   debugger
      //     console.log(err);
      //     console.log(err.message);
      //     let errMsg = err.message ? err.message : err;
      //     _this.$message.error("错误信息：" + errMsg, 5);
      //     // _this.confirmLoading = false;
      //   });
      //重置
    },
    handleCancel(e) {
      this.visible = false;
    },
  }
};
</script>



