              </a-col>
              <a-col :md="6" :sm="24">
                <a-form-item label="状态">
                  <a-select v-decorator="['status']">
                    <a-select-option value="">请选择</a-select-option>
                    <a-select-option v-for="(value,key) in accounts_dictionary.status" :key="key" :value="key">{{value}}</a-select-option>
                  </a-select>
                </a-form-item>
              </a-col>
              <a-col :md="6" :sm="24">
                <a-form-item label="批次">
                  <a-select v-decorator="['batch']">
                    <a-select-option value="">请选择</a-select-option>
                    <a-select-option v-for="(value,key) in accounts_dictionary.batch" :key="key" :value="key">{{value}}</a-select-option>
                  </a-select>
                </a-form-item>
              </a-col>
              <a-col :md="6" :sm="24">
                <a-form-item label="意向程度">
                  <a-select v-decorator="['intention']">
                    <a-select-option value="">请选择</a-select-option>
                    <a-select-option v-for="(value,key) in accounts_dictionary.intention" :key="key" :value="key">{{value}}</a-select-option>
