                <span class="table-page-search-submitButtons interval-sovle" :style="advanced && { float: 'right', overflow: 'hidden' } || {} ">
                  <a id="advanced" @click="toggleAdvanced" style="margin-right: 8px">
                    {{ advanced ? '收起' : '展开' }}
                    <a-icon :type="advanced ? 'up' : 'down'"/>
                  </a>
