            <!-- <a-button class="filter-button" v-if="isDesktop()" @click.native="showCurrentMonthDue">
              本月到期
            </a-button> -->
            <a-dropdown v-if="!isDesktop()">
              <a-menu slot="overlay" @click="handleFilterMenuClick">
                <a-menu-item @click="showTodayAppointment" key="1">
                  <a-icon v-show="todayAptLoading" type="loading" />
                  今日预约
                </a-menu-item>
                <a-menu-item @click="showTomorrowAppointment" key="2">
                  <a-icon v-show="tomorrowAptLoading" type="loading" />
                  明日预约
                </a-menu-item>
                <a-menu-item @click="showDayAfterTomorrowAppointment" key="3">
                  <a-icon v-show="afterTomorrwAptLoading" type="loading" />
                  后日预约
                </a-menu-item>
                <a-menu-item @click="showCurrentMonthDue" key="4">本月到期</a-menu-item>
              </a-menu>
              <a-button><a-icon type="ellipsis"/></a-button>
            </a-dropdown>
          </a-button-group>
        </div>
        <!-- <div class="interval-sovle" style="float:right">
          <div class="interval-sovle" style="display:inline-block;margin-right:24px">
            <a style="margin-right:8px">1-10号</a>
            <a style="margin-right:8px">10-20号</a>
            <a>20-30号</a>
          </div>
          <range-picker :style="{width: '256px'}" />
        </div> -->
      </div>
      <div class="table-operator">
        <a-table
          :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
          :pagination="pager"
          :columns="columns"
          :dataSource="customersInfo"
          @change="handleChange"
          :scroll="{ x: 1300 }"
          :loading="loadingTable"
        >
      <template slot="owner" slot-scope="text, record">
        <router-link to="/customer/detail">
          <a href="javascript:;" @click="getCustomerInfo(record.id,record.owner,record.mobile)">
                      {{record.owner}}
                    </a>
        </router-link>
      </template>
      <template slot="mobile" slot-scope="text, record">
        <a href="javascript:;" @click="zswitch_callcenter_click_call_b(record.mobile)">
                    {{record.mobile}}
                  </a>
      </template>
      <template slot="operation" slot-scope="text, record">
        <a href="javascript:;" @click="onDelete(record.key)">删除</a>
      </template>
        </a-table>
      </div>
    </div>
  </page-layout>
</template>

<script>
  import AButton from "ant-design-vue/lib/button";
  import ATable from "ant-design-vue/lib/table";
  import AForm from "ant-design-vue/lib/form";
  import ARow from "ant-design-vue/lib/row";
  import ACol from "ant-design-vue/lib/col";
  import AInput from "ant-design-vue/lib/input";
  import ASelect from "ant-design-vue/lib/select";
  import AIcon from "ant-design-vue/lib/icon";
  import ADatePicker from "ant-design-vue/lib/date-picker";
  import ADropdown from 'ant-design-vue/lib/dropdown'
  import AMenu from 'ant-design-vue/lib/menu'
  import SearchCustomerInfoModal from "./SearchCustomerInfoModal.vue";
  import BatchModifyModal from "./BatchModifyModal.vue";
  import ImportCustomerInfoModal from "./ImportCustomerInfoModal.vue";
  import BatchDeleteModal from "./BatchDeleteModal.vue";
  import AddCustomerInfo from "./AddCustomerInfo.vue";
  import {
    getAccountsInfoList
  } from "@/api/accounts";
  import {
    mapGetters,
    mapMutations
  } from "vuex";
  import {mixin, mixinDevice} from '@/utils/mixin.js'
  import PageLayout from "@/components/Page/PageLayout";
  const AFormItem = AForm.Item;
  const ASelectOption = ASelect.Option;
  const AButtonGroup = AButton.Group;
  const AMenuItem = AMenu.Item
  const {RangePicker} = ADatePicker
  const columns = [{
      title: "姓名",
      dataIndex: "owner",
      // width: 100,
      key: "owner",
      // fixed: "left",
      // filters: [{
      //     text: "2",
      //     value: "2"
      //   },
      //   {
      //     text: "xiao",
  
      //     value: "xiao"
      //   }
      // ],
      scopedSlots: {
        customRender: "owner"
      },
      sorter: true
    },
    {
      title: "手机",
      dataIndex: "mobile",
      key: "mobile",
      scopedSlots: {
        customRender: "mobile"
      }
    },
    {
      title: "车牌",
      dataIndex: "plate_no",
      key: "plate_no"
    },
    {
      title: "品牌",
      dataIndex: "team",
      key: "team"
    },
    {
      title: "注册日期",
      dataIndex: "register_date",
      key: "register_date",
      scopedSlots: {
        customRender: "register_date"
      },
      sorter: true
    },
    {
      title: "保险公司",
      dataIndex: "company",
      key: "company"
    },
    {
      title: "商业到期",
      dataIndex: "business_expiration_date",
      key: "business_expiration_date",
      onFilter: true,
      sorter: true
    },
    {
      title: "预约时间",
      dataIndex: "preset_time",
      key: "preset_time",
      sorter: true
    },
    {
      title: "状态",
      dataIndex: "status",
      key: "status",
      filters: [{
          text: "首拨",
          value: "FIRST_DIAL"
        },
        {
          text: "成功",
          value: "成功"
        },
        {
          text: "失败",
          value: "失败"
        },
        {
          text: "失效",
          value: "失效"
        },
        {
          text: "预约已报价",
          value: "预约已报价"
        },
        {
          text: "预约未报价",
          value: "预约未报价"
        }
      ]
    },
    {
      title: "批次",
      dataIndex: "batch",
      key: "batch",
      filters: [{
          text: "A",
          value: "A"
        },
        {
          text: "B",
          value: "B"
        },
        {
          text: "C",
          value: "C"
        },
        {
          text: "D",
          value: "D"
        },
        {
          text: "E",
          value: "E"
        },
        {
          text: "F",
          value: "F"
        },
        {
          text: "G",
          value: "G"
        },
        {
          text: "H",
          value: "H"
        }
      ]
    },
    {
      title: "类型",
      dataIndex: "type",
      key: "type"
    },
    {
      title: "备注",
      dataIndex: "remark",
      key: "remark"
    },
    {
      title: "归属于",
      dataIndex: "user_attach",
      key: "user_attach"
    },
    {
      title: "操作",
      key: "operation",
      dataIndex: "operation",
      // fixed: "right",
      // width: 100,
      scopedSlots: {
        customRender: "operation"
      }
    }
  ];
  export default {
    // props: {
    //   pageSizeOptions: {
    //     type: Array,
    //     default () {
    //       return ["10", "20", "30", "40", "50"];
    //     }
    //   }
    // },
    mixins:[mixin,mixinDevice],
    components: {
      // AInput:Input,
      AButtonGroup,
      AButton,
      ATable,
      SearchCustomerInfoModal,
      BatchModifyModal,
      ImportCustomerInfoModal,
      BatchDeleteModal,
      AddCustomerInfo,
      AFormItem,
      AForm,
      ARow,
      ACol,
      AInput,
      ASelect,
      ASelectOption,
      AIcon,
      ADatePicker,
      RangePicker,
      ADropdown,
      AMenu,
      AMenuItem,
      PageLayout
    },
    data() {
      return {
        customersInfo: [],
        // customerNum,//当前列表中客户数量，用于批量修改和删除的当前搜索的数量统计
        //        columns,
        selectedRowKeys: [], // Check here to configure the default column
        selectedRowId: [],//被选中行的id的数组
        // loading: false,
        speedyQueryLoading: false, //快捷查询按钮loading
        resetHandleLoading: false, //重置按钮loading
        loadingTable: false, //列表loading
        allLoading: false, //'全部'按钮loading
        firstDialLoading: false, //首拨按钮loading
        todayAptLoading: false, //今日预约按钮loading
        tomorrowAptLoading: false, //明日预约按钮loading
        afterTomorrwAptLoading: false, //后日预约按钮loading
        isFilterLoading: false, //筛选中的按钮只要有一个loading为true，此变量均为true，并使得筛选所有按钮不可点击
        filteredInfo: null,
        sortedInfo: null,
        columns,
  
        total: 0,
        defaultCurrent: 1,
        current_page: 0,
        pageSize: 0,
        currentPageSize: this.pageSize,
  
        querySuccess: false,
        // 高级搜索 展开/关闭
        advanced: false,
        accounts_dictionary: this.$localstorage.getLocalStorage(
          "accounts_dictionary"
        )
      };
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    created() {
      let _this = this;
      this.getCustomerInfoDateFromServer()
        .then(result => {
          _this.getDictionaryFromServer();
        })
        .catch(err => {});
      // this.loading = true;
      // setTimeout(() => {
      //   this.usersInfoInit();
      //   this.loading = false;
      // }, 1000);
    },
    watch: {
      $route(to, from) {
        if (to.path == "/customer/list") {
          this.condition.operation = "";
          const params = {
            where: this.condition.where,
            order: this.condition.order,
            orderby: this.condition.orderby,
            page: this.condition.page
          };
          this.getCustInfoAccConditionFromServer(params)
            .then(result => {
            })
            .catch(err => {});
        }
      }
    },
    computed: {
      ...mapGetters(["currentCustInfo", "condition"]),
      title() {
        return this.$route ? this.$route.meta.title : "";
      },
      hasSelected() {
        if (!this.selectedRowKeys) {
          return false;
        }
        return this.selectedRowKeys.length > 0;
      },
      customerNum() {
        return this.total;
      },
      pager() {
        return {
          total: this.total,
          showTotal: total => `共有 ${total} 条`,
          // showSizeChanger: true,
          showQuickJumper: true,
          // pageSizeOptions: this.pageSizeOptions,
          pageSize: this.pageSize,
          defaultCurrent: this.defaultCurrent,
          current: this.current_page
          // onShowSizeChange: this.onPagerSizeChange
        };
      }
    },
    methods: {
      ...mapMutations({
        setCurrentCustInfo: "CURRENT_CUST_INFO",
        setCondition: "CONDITION"
      }),
      handleMenuClick(e) {
        console.log('click', e);
      },
      handleFilterMenuClick(e) {
        console.log('click', e);
      },
      /**
       * @description: 调用软电话拨号
       * @param {String} mobile 电话
       * @return: 
       */
      zswitch_callcenter_click_call_b(mobile) {
        window.location.href = "sip:" + mobile;
      },
      /**
       * @description: 便捷查询
       * @param {Array} where 查询条件 
       * @param {Object} params 包含where、排序方式和页码的查询参数 
       * @return: 
       */
      speedyQuery() {
        let _this = this;
        let where = [];
        this.form.validateFields((err, values) => {
          if (!err) {
            for (let item in values) {
              if (!values[item]) {
                delete values[item];
              } else {
                where.push({
                  field: item,
                  condition: "=",
                  value: values[item],
                  link: "and"
                });
              }
            }
            const params = {
              where: JSON.stringify(where),
              orderby: "",
              order: "",
              page: ""
            };
            _this.speedyQueryLoading = true;
            _this.isFilterLoading = true;
            _this.getCustInfoAccConditionFromServer(params);
          }
        });
      },
      /**
       * @description: 重置查询
       * @param {Object} params 包含where、排序方式和页码的查询参数 
       * @return: 
       */
      resetQuery() {
        this.form.resetFields();
        const params = {
          where: "",
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.resetHandleLoading = true;
        this.isFilterLoading = true;
        this.getCustInfoAccConditionFromServer(params);
      },
      /**
       * @description: 便捷查询条件展开开关
       * @param {Boolean} advanced 查询条件展开开关参数
       * @return: 
       */
      toggleAdvanced() {
        this.advanced = !this.advanced;
      },
      /**
       * @description: accounts字典查询
       * @param {String} dictionaryName 字典名 
       * @return: 
       */
      getDictionaryFromServer() {
        let _this = this;
        return new Promise(function(resolve, reject) {
          const dictionaryName = "accounts";
          _this.$api
            .getDictionary(dictionaryName)
            .then(result => {
              _this.$localstorage.putLocalStorage(
                "accounts_dictionary",
                result.data
              );
              resolve(result);
            })
            .catch(err => {
              _this.$myNotification.sendFailedNotification(err)
              reject(err);
            });
        });
      },
      /**
       * @description: 重新获取客户资料列表数据
       * @param {Object} params 包含where、排序方式和页码的查询参数 
       * @return: 
       */
      refreshCustInfo() {
        const params = {
          where: this.condition.where,
          order: this.condition.order,
          orderby: this.condition.orderby,
          page: this.condition.page
        };
        this.getCustInfoAccConditionFromServer(params)
          .then(result => {
            this.selectedRowKeys = [];
            this.selectedRowId = [];
          })
          .catch(err => {});
      },
      /**
       * @description: 客户资料查询
       * @param {Object} params 包含where、排序方式和页码的查询参数 
       * @return: 
       */
      queryCustInfo(SCResult) {
        let _this = this;
        const params = {
          where: JSON.stringify(SCResult),
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.$refs.dialogtreeSearchCustomerInfo.confirmLoading = true;
        this.getCustInfoAccConditionFromServer(params)
          .then(result => {
            _this.$refs.dialogtreeSearchCustomerInfo.confirmLoading = false;
            _this.$refs.dialogtreeSearchCustomerInfo.visible = false;
          })
          .catch(err => {
            _this.$refs.dialogtreeSearchCustomerInfo.confirmLoading = false;
          });
      },
      /**
       * @description: 获取全部客户资料列表的接口函数
       * @param {type} 
       * @return: 
       */
      getCustomerInfoDateFromServer() {
        let _this = this;
        this.loadingTable = true;
        return new Promise(function(resolve, reject) {
          getAccountsInfoList()
            .then(result => {
              _this.customersInfo = [];
              result.data.forEach((item, index) => {
                _this.customersInfo.push({
                  key: index,
                  id: item.id,
                  owner: item.owner,
                  mobile: item.mobile,
                  plate_no: item.plate_no,
                  team: item.team,
                  register_date: item.register_date,
                  business_expiration_date: item.business_expiration_date,
                  company: item.company,
                  preset_time: item.preset_time,
                  status: item.status,
                  batch: item.batch,
                  type: item.type,
                  remark: item.remark,
                  user_attach: item.user_attach
                });
                // }
              });
              _this.current_page = result.current_page;
              _this.pageSize = result.per_page;
              _this.total = result.total;
              _this.loadingTable = false;
              resolve(result);
            })
            .catch(err => {
              _this.loadingTable = false;
              _this.$myNotification.sendFailedNotification(err)
              reject(err);
            });
        });
      },
      // //分页
      // onPagerSizeChange(current) {
      //   // this.current = current;
      //   this.currentPageSize = size;
      // },
      /**
       * @description: 批量删除
       * @param {type} 
       * @return: 
       */
      batchDelete() {
        this.$refs.dialogtreeBatchDelete.showModal();
      },
      /**
       * @description: 表格选中项变化时的回调
       * @param {Array} selectedRowKeys 被选中行的key值的数组 
       * @return: 
       */
      onSelectChange(selectedRowKeys) {
        this.selectedRowId = [];
        for (let i = 0; i < selectedRowKeys.length; i++) {
          const target = this.customersInfo.filter(
            item => item.key === selectedRowKeys[i]
          )[0];
          this.selectedRowId.push(target.id);
        }
        this.selectedRowKeys = selectedRowKeys;
      },
      /**
       * @description: 根据条件获取客户资料列表的接口函数
       * @param {type} 
       * @return: 
       */
      getCustInfoAccConditionFromServer(params) {
        let _this = this;
        this.loadingTable = true;
        return new Promise(function(resolve, reject) {
          _this.$api
            .getCustInfoAccCondition(params)
            .then(result => {
              //清空cusomersInfo
              _this.customersInfo = [];
              result.data.data.forEach((item, index) => {
                _this.customersInfo.push({
                  key: index,
                  id: item.id,
                  owner: item.owner,
                  mobile: item.mobile,
                  plate_no: item.plate_no,
                  team: item.team,
                  register_date: item.register_date,
                  business_expiration_date: item.business_expiration_date,
                  company: item.company,
                  preset_time: item.preset_time,
                  status: item.status,
                  batch: item.batch,
                  type: item.type,
                  remark: item.remark,
                  user_attach: item.user_attach
                });
              });
              _this.current_page = result.data.current_page;
              _this.pageSize = result.data.per_page;
              _this.total = result.data.total;
              (_this.speedyQueryLoading = false),
              (_this.resetHandleLoading = false),
              (_this.loadingTable = false);
              _this.allLoading = false;
              _this.firstDialLoading = false;
              _this.todayAptLoading = false;
              _this.tomorrowAptLoading = false;
              _this.afterTomorrwAptLoading = false;
              _this.isFilterLoading = false;
              _this.$notification['success']({
                message: '查询成功',
                description: '共查询到'+_this.total+'条名单'
              })
              resolve(result);
            })
            .catch(err => {
              _this.customersInfo = [];
              _this.current_page = 0;
              _this.pageSize = 0;
              _this.total = 0;
              (_this.speedyQueryLoading = false),
              (_this.resetHandleLoading = false),
              (_this.loadingTable = false);
              _this.allLoading = false;
              _this.firstDialLoading = false;
              _this.todayAptLoading = false;
              _this.tomorrowAptLoading = false;
              _this.afterTomorrwAptLoading = false;
              _this.isFilterLoading = false;
              _this.setCondition({
                where: "",
                orderby: "",
                order: "",
                page: ""
              });
              _this.$myNotification.sendFailedNotification(err)
              reject(err);
            });
        });
      },
      /**
       * @description: 分页、排序、筛选变化时触发
       * @param {Array} whereArr 查询条件，用以区分下面where
       * @return: 
       */
      handleChange(pagination, filters, sorter) {
        let whereArr = [];
        for (var key in filters) {
          if (filters[key]) {
            whereArr.push({
              field: key,
              condition: "like",
              value: filters[key][0],
              link: "and"
            });
          }
        }
        //如果whereArr数组中全部元素的value都为空，则将whereArr清空
        if (whereArr.length) {
          whereArr = whereArr.filter(item => item.value);
        }
        const params = {
          where: whereArr.length ?
            JSON.stringify(whereArr) :
            this.condition.where,
          orderby: sorter.columnKey ? sorter.columnKey : this.condition.orderby,
          order: sorter.order ? sorter.order : this.condition.order,
          page: pagination.current ? pagination.current : this.condition.page
        };
        this.setCondition(params);
        this.filteredInfo = filters;
        this.sortedInfo = sorter;
        //翻页、筛选、排序时的请求操作
        this.getCustInfoAccConditionFromServer(params);
      },
      /**
       * @description: 显示全部
       * @param {type} 
       * @return: 
       */
      showAll() {
        let _this = this
        const params = {
          where: "",
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.allLoading = true;
        this.isFilterLoading = true;
        return new Promise((resolve,reject) => {
          _this.getCustInfoAccConditionFromServer(params).then((result) => {
            return true
          }).catch((err) => {
            return false
          });
        })
      },
      /**
       * @description: 显示状态为首拨的columns
       * @param {type} 
       * @return: 
       */
      showFirstDial() {
        const params = {
          where: JSON.stringify([{
            field: "status",
            condition: "=",
            value: "FIRST_DIAL",
            link: "and"
          }]),
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.firstDialLoading = true;
        this.isFilterLoading = true;
        this.getCustInfoAccConditionFromServer(params);
      },
      /**
       * @description: 删除客户资料的接口函数
       * @param {type} 
       * @return: 
       */
      sendDeleteDataToServer(id) {
        let _this = this;
        this.loadingTable = true;
        this.$api
          .deleteCustomerInfo(id)
          .then(result => {
            const params = {
              where: _this.condition.where,
              order: _this.condition.order,
              orderby: _this.condition.orderby,
              page: _this.condition.page
            };
            _this
              .getCustInfoAccConditionFromServer(params)
              .then(result => {})
              .catch(err => {
                reject(err);
              });
          })
          .catch(err => {
            _this.loadingTable = false;
            _this.$myNotification.sendFailedNotification(err)
          });
      },
      /**
       * @description: 删除客户资料的点击事件
       * @param {type} 
       * @return: 
       */
      onDelete(key) {
        let _this = this;
        this.$confirm({
          title: "你确定要删除该客户信息吗?",
          content: "Some descriptions",
          okText: "确定",
          okType: "danger",
          cancelText: "取消",
          onOk() {
            // _this.loading = true;
            // ajax request after empty completing
            // setTimeout(() => {
            //   _this.loading = false;
            //   const customersInfo = [..._this.customersInfo];
            //   _this.customersInfo = customersInfo.filter(
            //     item => item.key !== key
            //   );
            // }, 1000);
            const id = _this.customersInfo.filter(item => item.key === key)[0].id;
            _this.sendDeleteDataToServer(id);
          },
          onCancel() {}
        });
  
        //        const customersInfo = [...this.customersInfo]
        //        this.customersInfo = customersInfo.filter(item => item.key !== key)
      },
      exportFile() {},
      /**
       * @description: 格式化日期
       * @param {Object} arg 日期
       * @return: 
       */
      getFormatDate(arg) {
        if (arg == undefined || arg == "") {
          return "";
        }
  
        var re = arg + "";
        if (re.length < 2) {
          re = "0" + re;
        }
        return re;
      },
      /**
       * @description: 获取今日日期
       * @param {type} 
       * @return: 
       */
      getTodayDate() {
        let date = new Date();
        date.setDate(date.getDate());
        let month = date.getMonth() + 1;
        let day = date.getDate();
        return (
          date.getFullYear() +
          "-" +
          this.getFormatDate(month) +                                                                                                                                                                                                                                 (month) +
          "-" +
          this.getFormatDate(day)
        );
      },
      /**
       * @description: 获取明日日期
       * @param {type} 
       * @return: 
       */
      getTomorrowDate() {
        let date = new Date();
        date.setDate(date.getDate() + 1);
        let month = date.getMonth() + 1;
        let day = date.getDate();
        return (
          date.getFullYear() +
          "-" +
          this.getFormatDate(month) +
          "-" +
          this.getFormatDate(day)
        );
      },
      /**
       * @description: 获取后日日期
       * @param {type} 
       * @return: 
       */
      getDayAfterTomorrowDate() {
        let date = new Date();
        date.setDate(date.getDate() + 2);
        let month = date.getMonth() + 1;
        let day = date.getDate();
        return (
          date.getFullYear() +
          "-" +
          this.getFormatDate(month) +
          "-" +
          this.getFormatDate(day)
        );
      },
      /**
       * @description: 获取当前月份
       * @param {type} 
       * @return: 
       */
      getCurrentMonth() {
        let date = new Date();
        date.setDate(date.getDate());
        let month = date.getMonth() + 1;
        return date.getFullYear() + "-" + this.getFormatDate(month);
      },
      /**
       * @description: 显示今日预约
       * @param {type} 
       * @return: 
       */
      showTodayAppointment() {
        const start = this.getTodayDate() + " 00:00:00";
        const end = this.getTodayDate() + " 23:59:59";
        const where = [{
            field: "preset_time",
            condition: ">=",
            value: start,
            link: "and"
          },
          {
            field: "preset_time",
            condition: "<=",
            value: end,
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "FAILED",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "FIRST_DIAL",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "INVALID",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "SUCCESS",
            link: "and"
          }
        ];
        const params = {
          where: JSON.stringify(where),
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.todayAptLoading = true;
        this.isFilterLoading = true;
        this.getCustInfoAccConditionFromServer(params);
      },
      /**
       * @description: 显示明日预约
       * @param {type} 
       * @return: 
       */
      showTomorrowAppointment() {
        const start = this.getTomorrowDate() + " 00:00:00";
        const end = this.getTomorrowDate() + " 23:59:59";
        const where = [{
            field: "preset_time",
            condition: ">=",
            value: start,
            link: "and"
          },
          {
            field: "preset_time",
            condition: "<=",
            value: end,
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "FAILED",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "FIRST_DIAL",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "INVALID",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "SUCCESS",
            link: "and"
          }
        ];
        const params = {
          where: JSON.stringify(where),
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.tomorrowAptLoading = true;
        this.isFilterLoading = true;
        this.getCustInfoAccConditionFromServer(params);
      },
      /**
       * @description: 显示后日预约
       * @param {type} 
       * @return: 
       */
      showDayAfterTomorrowAppointment() {
        const start = this.getDayAfterTomorrowDate() + " 00:00:00";
        const end = this.getDayAfterTomorrowDate() + " 23:59:59";
        const where = [{
            field: "preset_time",
            condition: ">=",
            value: start,
            link: "and"
          },
          {
            field: "preset_time",
            condition: "<=",
            value: end,
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "FAILED",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "FIRST_DIAL",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "INVALID",
            link: "and"
          },
          {
            field: "status",
            condition: "!=",
            value: "SUCCESS",
            link: "and"
          }
        ];
        const params = {
          where: JSON.stringify(where),
          orderby: "",
          order: "",
          page: ""
        };
        this.setCondition(params);
        this.afterTomorrwAptLoading = true;
        this.isFilterLoading = true;
        this.getCustInfoAccConditionFromServer(params);
      },
      /**
       * @description: 显示本月到期
       * @param {type} 
       * @return: 
       */
      showCurrentMonthDue() {
        // this.filteredInfo = {
        //   expiration_date: [this.getCurrentMonth()]
        // };
        this.$notification['warning']({
                  message: '该条件不明确，待求证',
                })
      },
      //      显示查询模态框
      showSearchModel() {
        this.$refs.dialogtreeSearchCustomerInfo.showSearchCustomerInfoModal();
      },
      //      显示导入模态框
      showImportModal() {
        this.$refs.dialogtreeImportCustomerInfo.showImportCustomerInfoModal();
      },
      //      显示批量删除模态框
      showbatchModifyModal() {
        this.$refs.dialogtreeBatchModify.showBatchModifyModal();
      },
      /**
       * @description: 获取选中的客户信息
       * @param {type} 
       * @return: 
       */
      getCustomerInfo(id, owner, mobile) {
        const info = {
          id,
          owner,
          mobile
        };
        // this.$store.commit("CURRENT_CUST_INFO", info);
        this.setCurrentCustInfo(info)
        // this.setID(id);
        // this.setName(name);
        // this.setTel(mobile);
      }
    }
  };
</script>


<style>
  .clearfix::after{
    clear: both
  }
  .editable-cell {
    position: relative;
  }
  
  .editable-cell-input-wrapper,
  .editable-cell-text-wrapper {
    padding-right: 24px;
  }
  
  .editable-cell-text-wrapper {
    padding: 5px 24px 5px 5px;
  }
  
  .editable-cell-icon,
  .editable-cell-icon-check {
    position: absolute;
    right: 0;
    width: 20px;
    cursor: pointer;
  }
  
  .editable-cell-icon {
    line-height: 18px;
    display: none;
  }
  
  .editable-cell-icon-check {
    line-height: 28px;
  }
  
  .editable-cell:hover .editable-cell-icon {
    display: inline-block;
  }
  
  .editable-cell-icon:hover,
  .editable-cell-icon-check:hover {
    color: #108ee9;
  }
  
  .editable-add-btn {
    margin-bottom: 8px;
  }
  
  .table-page-search-wrapper .ant-form-inline .ant-form-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 24px;
    margin-right: 0;
  }
  
  .table-page-search-wrapper .ant-form-inline .ant-form-item>div.ant-form-item-label {
    width: 80px;
  }
  
  .customerInfo .table-page-search-submitButtons {
    margin-bottom: 0;
  }
</style>



