<template>
  <div class="uploadDataFile">
    <ul style="list-style-type: none;font-size: 12px;font-weight: 500">
      <li>导入数据前请按规范整理导入数据文件，具体要求请参考帮助文档，或联系技术支持。</li>
      <li>数据文件可以是EXECL文档或文本文件（推荐），扩展名:xls,xlsx,csv,txt。</li>
      <li>建议将文本文件保存为“UTF-8 无BOM”格式。</li>
    </ul>
    <a-divider/>
    <div>
      <a-button @click="onImportChange">导入已上传文件</a-button>
      <a-button>清理上传文件</a-button>
    </div>
    <a-divider/>
    <div :class="importIsDisplay ? 'isVisible':'isHidden'">
      <a-upload action="//jsonplaceholder.typicode.com/posts/" :fileList="fileList" @remove="handleRemove" :beforeUpload="beforeUpload">
        <a-button>
            <a-icon type="upload"/>
            选择文件
          </a-button>
      </a-upload>
      <a-button class="upload-demo-start" type="primary" @click="handleUpload" :disabled="fileList.length === 0" :loading="uploading">
          {{uploading ? '正在上传' : '开始上传' }}
        </a-button>
    </div>
    <div :class="importIsDisplay ? 'isHidden':'isVisible'">
      <a-select defaultValue="" style="width: 120px" @change="handleSelectedFileChange">
          <a-select-option disabled value="">请选择文件</a-select-option>
        </a-select>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import ADivider from 'ant-design-vue/lib/divider'
  import AButton from 'ant-design-vue/lib/button'
  import AUpload from 'ant-design-vue/lib/upload'
  import ASelect from 'ant-design-vue/lib/select'
  import AIcon from 'ant-design-vue/lib/icon'
  import reqwest from 'reqwest'
  const ASelectOption = ASelect.Option
  
  const dataSource = [];
  for (let i = 0; i < 5; i++) {
    if (i < 5) {
      dataSource.push({
        key: i,
        owner: `${i}Edward`,
        mobile: 13888888888,
        plate_no: `川A00S0${i}`,
        team: '',
        register_date: `2018-08-${i}`,
        business_expiration_date: `2018-08-${i} ${i}:00:00`,
        company: `未知`,
        preset_time: `2018-09-${i} ${i}:00:00`,
        status: '预约已报价',
        batch: 'A',
        type: '首年',
        remark: `六月17${i}中旬`,
        user_attach: `600${i}`,
  
      });
    }
  }
  
  export default {
    components: {
      reqwest,
      ADivider,
      AButton,
      AUpload,
      ASelect,
      ASelectOption,
      AIcon
    },
    data() {
      return {
        fileList: [],
        uploading: false,
        importIsDisplay: true,
        dataSource,
      }
    },
    methods: {
  
      handleRemove(file) {
        const index = this.fileList.indexOf(file);
        const newFileList = this.fileList.slice();
        newFileList.splice(index, 1);
        this.fileList = newFileList
      },
      beforeUpload(file) {
        this.fileList = [...this.fileList, file]
        return false;
      },
      handleUpload() {
        const {
          fileList
        } = this;
        const formData = new FormData();
        fileList.forEach((file) => {
          formData.append('files[]', file);
        });
        this.uploading = true
  
        // You can use any AJAX library you like
        reqwest({
          url: '//jsonplaceholder.typicode.com/posts/',
          method: 'post',
          processData: false,
          data: formData,
          success: () => {
            this.fileList = []
            this.uploading = false
            this.$notification['success']({
                  message: 'upload successfully.',
                })
          },
          error: () => {
            this.uploading = false
            this.$notification['warning']({
                  message: 'upload failed.',
                })
          },
        });
      },
      onImportChange() {
        this.importIsDisplay = !this.importIsDisplay
      },
      handleSelectedFileChange(value) {
      },
    }
  }
</script>

<style>
  .upload-demo-start {
    margin-top: 16px;
  }
</style>



