<template>
  <div class="importSettings">
    <p>导入参数</p>
    <a-row class="importParame">
      <a-col class="gutter-row" :lg="12" :md="12" :xs="24">
        <div>
          <label>第一行是标题：</label>
          <a-checkbox :checked="checkedFirstLineTitle" @change="onFirstLineTitleChange"/>
        </div>
        <div>
          <label>标题匹配字段：</label>
          <a-checkbox :checked="checkedTitleMatchField" :disabled="disabledTitleMatchField"
                      @change="onTitleMatchFieldChange"/>
        </div>
        <div>
          <label>导入"工作表"：</label>
          <a-select defaultValue="customerInfo" size="small" style='width: 100px' @change="handleChange">
            <a-select-option value="customerInfo">客户资料</a-select-option>
          </a-select>
        </div>
      </a-col>
      <a-col class="gutter-row" :lg="12" :md="12" :xs="24">
        <div>
          <label>检查重复记录：</label>
          <a-checkbox :checked="checkedCheckDuplicateRecord" @change="onCheckDuplicateRecordChange"/>
          <a-select :disabled="disabledCheckDuplicateRecord" defaultValue="vin" size="small" style='width: 100px' @change="handleChange">
            <a-select-option v-for="(item,index) in data" :key="index" :value="item.key">{{item.value}}</a-select-option>
          </a-select>
        </div>
        <div>
          <label>重复处理方法：</label>
          <a-select defaultValue="discardedRecord" size="small" style='width: 100px' @change="handleChange">
            <a-select-option value="discardedRecord">丢弃记录</a-select-option>
            <a-select-option value="updateRecord">更新记录</a-select-option>
          </a-select>
        </div>
      </a-col>
    </a-row>
    <a-divider/>
    <a-table :columns="columns" :dataSource="getDataSource"  @change="handleTableChange" :scroll="{ x: 1300 }" :pagination="false">
      <!--<template slot="name" slot-scope="text, record">-->
      <!--<EditableCell :text="text" @change="onCellChange(record.key, 'name')"/>-->
      <!--</template>-->

    </a-table>
  </div>
</template>

<script>
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import ACheckbox from 'ant-design-vue/lib/checkbox'
  import ASelect from 'ant-design-vue/lib/select'
  import ADivider from 'ant-design-vue/lib/divider'
  import ATable from 'ant-design-vue/lib/table'
  const ASelectOption=ASelect.Option
  export default{
      props:{
        getDataSource:Array,
      },
    components:{
      ARow,
      ACol,
      ACheckbox,
      ASelect,
      ASelectOption,
      ADivider,
      ATable
    },
    data(){
      return {
        disabledTitleMatchField: false,
        disabledCheckDuplicateRecord: false,
        checkedFirstLineTitle: true,
        checkedTitleMatchField: true,
        checkedCheckDuplicateRecord: true,
        data: [
          {key:'vin',value: 'VIN码'},
          {key:'mobile',value: '手机'},
          {key:'plate_no',value: '车牌'},
          {key:'id_code',value: '证件号'},
        ],
        dataSource:[],
      }
    },
    mounted(){
    },
    computed:{
      columns() {
        let {sortedInfo, filteredInfo} = this;
        sortedInfo = sortedInfo || {};
        filteredInfo = filteredInfo || {};
        const columns = [{
          title: '姓名',
          dataIndex: 'owner',
          width: 100,
          key: 'owner',
          fixed: 'left',
        }, {
          title: '手机',
          dataIndex: 'mobile',
          key: 'mobile',
        }, {
          title: '车牌',
          dataIndex: 'plate_no',
          key: 'plate_no',
        }, {
          title: '品牌',
          dataIndex: 'team',
          key: 'team',
        }, {
          title: '注册日期',
          dataIndex: 'register_date',
          key: 'register_date',
        }, {
          title: '保险公司',
          dataIndex: 'company',
          key: 'company',
        }, {
          title: '商业到期',
          dataIndex: 'business_expiration_date',
          key: 'business_expiration_date',
        }, {
          title: '预约时间',
          dataIndex: 'preset_time',
          key: 'preset_time',
        }, {
          title: '状态',
          dataIndex: 'status',
          key: 'status',
        }, {
          title: '批次',
          dataIndex: 'batch',
          key: 'batch',
        }, {
          title: '类型',
          dataIndex: 'type',
          key: 'type',
        }, {
          title: '备注',
          dataIndex: 'remark',
          key: 'remark',
        }, {
          title: '归属于',
          dataIndex: 'user_attach',
          key: 'user_attach',
        }, {
          title: '操作',
          key: 'operation',
          dataIndex: 'operation',
          fixed: 'right',
          width: 100,
          scopedSlots: {customRender: 'operation'},
        }];
        return columns;
      }
    },
    methods: {
      onFirstLineTitleChange (e) {
        this.checkedFirstLineTitle = e.target.checked
        this.disabledTitleMatchField = !this.disabledTitleMatchField
      },
      onTitleMatchFieldChange (e) {

        this.checkedTitleMatchField = e.target.checked
      },
      onCheckDuplicateRecordChange (e) {

        this.checkedCheckDuplicateRecord = e.target.checked
        this.disabledCheckDuplicateRecord = !this.disabledCheckDuplicateRecord
      },
      handleChange(){

      },
      handleTableChange(pagination, filters, sorter) {
        this.filteredInfo = filters;
        this.sortedInfo = sorter;
      },
    }
  }
</script>

<style>
  .importSettings {
    font-size: 12px;
  }

  .importSettings > p {
    font-weight: 800;
  }

  .importParame > div>div {
    padding-top: 8px;
  }
</style>



