<template>
  <div class="batchModifyModal">
    <!--<a-button type="primary" @click="showModal"> New Tag</a-button>-->
    <a-modal title="批量修改" :visible="visible" :maskClosable="maskClosable" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel" width="530px">
      <a-form :form="form">
        <a-alert type="info" :show-icon="true">
          <div slot="message">
            已选中&nbsp;<a style="font-weight: 600">{{list_Num}}</a>&nbsp;条符合筛选条件的客户资料&nbsp;&nbsp;
          </div>
        </a-alert>
        <div class="condition_container">
          <span>筛选条件：</span>
          <a-form-item style="margin-right:5px">
            <a-select v-decorator="['operation',{initialValue:'select'}]" :size="size" style="width: 100px" @change="handleChange">
              <a-select-option value="select">当前选择</a-select-option>
              <a-select-option value="search">当前搜索</a-select-option>
              <a-select-option value="all">全部记录</a-select-option>
            </a-select>
          </a-form-item>
          <span style="margin-right:5px">
            <a-divider type="vertical" /> 前
            </span>
          <a-form-item>
            <a-input-number v-decorator="['limit',{initialValue:0}]" :size="size" :min="0" :max="limitMax" style="width: 60px" :disabled="disabledPart" @change="inputNumberChange" />
          </a-form-item>
          <span style="margin-right:5px">
            条
            <a-divider type="vertical" />
            </span>
          <a-form-item>
            <a-checkbox v-decorator="['all_record',{initialValue:true}]" defaultChecked @change="onAllCheckBoxChange">全部（筛选条件）</a-checkbox>
          </a-form-item>
        </div>
        <a-divider />
        <div class="modifyContent">
          <div class="modifyItem">
            <!-- <a-form-item fieldDecoratorId="user_attach_checkbox"> -->
            <a-checkbox @change="onUserAttachCheckBoxChange" />
            <!-- </a-form-item> -->
            <!-- <a-form-item
                    label="归属于"
                    :labelCol="{ span: 5 }"
                    :wrapperCol="{ span: 12 }"
                    fieldDecoratorId="user_attach"
                  > -->
            <div style="display:flex;width:100%">
              <a-form-item label="归属于" class="form-item-child-one" style="margin-bottom:0;margin-right: 5px;">
                <a-select v-decorator="['user_attach_share_type']" :disabled="disabledUserAttach" @change="handleUserAttachChange" placeholder='请选择'>
                  <a-select-option value="USER">用户</a-select-option>
                  <a-select-option value="GROUP">工作组</a-select-option>
                </a-select>
              </a-form-item>
              <a-form-item style="margin-bottom:0;flex:1">
                <a-select v-decorator="['user_attach_users']" :disabled="disabledUserAttach" @change="handleUserAttachUserChange" placeholder='请选择'>
                  <a-select-option value="1">admin</a-select-option>
                  <a-select-option value="5">6000</a-select-option>
                </a-select>
              </a-form-item>
            </div>
            <!-- </a-form-item> -->
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onStatusCheckBoxChange" />
            <a-form-item label="状态" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-select v-decorator="['status']" :disabled="disabledStatus" @change="handleStatusChange" placeholder='请选择'>
                <a-select-option value="">请选择</a-select-option>
                <a-select-option value="FIRST_DIAL">首拨</a-select-option>
                <a-select-option value="FAILED">失败</a-select-option>
                <a-select-option value="INVALID">无效</a-select-option>
                <a-select-option value="APPOINTMENT_QUOTATION">预约已报价</a-select-option>
                <a-select-option value="APPOINTMENT_NON_QUOTATION">预约未报价</a-select-option>
                <a-select-option value="SUCCESS">成功</a-select-option>
              </a-select>
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onReportCheckBoxChange" />
            <a-form-item label="销售说明" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-select v-decorator="['report']" :disabled="disabledReport" @change="handleReportChange" placeholder='请选择'>
              </a-select>
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onTypeCheckBoxChange" />
            <a-form-item label="类型" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-select v-decorator="['type']" :disabled="disabledType" @change="handleTypeChange" placeholder='请选择'>
                <a-select-option value="FIRST_YEAR">首年</a-select-option>
                <a-select-option value="RENEWAL">续保</a-select-option>
                <a-select-option value="ORPHAN">孤儿单</a-select-option>
                <a-select-option value="LOAN_RENEWAL">车贷续保</a-select-option>
              </a-select>
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onCompanyCheckBoxChange" />
            <a-form-item label="保险公司" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-select v-decorator="['company']" :disabled="disabledCompany" @change="handleCompanyChange" placeholder='请选择'>
                <a-select-option value="PAIC">平安保险</a-select-option>
                <a-select-option value="PICC">PICC</a-select-option>
              </a-select>
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onExpireCheckBoxChange" />
            <a-form-item label="商业到期" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-date-picker v-decorator="['business_expiration_date']" style="width:100%" format="YYYY-MM-DD HH:mm:ss" :disabled="disabledExpire" />
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onAppointCheckBoxChange" />
            <a-form-item label="预约时间" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
  
              <a-date-picker v-decorator="['preset_time']" showTime style="width:100%" format="YYYY-MM-DD HH:mm:ss" :disabled="diabaledAppoint" />
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onBankCheckBoxChange" />
            <a-form-item label="贷款银行" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-input v-decorator="['lending_bank']" :disabled="disabledBank" placeholder="请输入" />
            </a-form-item>
          </div>
          <div class="modifyItem">
            <a-checkbox @change="onBatchCheckBoxChange" />
            <a-form-item label="批次" :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
              <a-select v-decorator="['batch']" :disabled="disabledBatch" @change="handleBatchChange" placeholder='请选择'>
                <a-select-option value="A">A</a-select-option>
                <a-select-option value="B">B</a-select-option>
                <a-select-option value="C">C</a-select-option>
                <a-select-option value="D">D</a-select-option>
                <a-select-option value="E">E</a-select-option>
                <a-select-option value="F">F</a-select-option>
                <a-select-option value="G">G</a-select-option>
                <a-select-option value="H">H</a-select-option>
              </a-select>
            </a-form-item>
          </div>
        </div>
      </a-form>
  
    </a-modal>
  
  </div>
</template>

<script>
  import AModal from 'ant-design-vue/lib/modal'
  import ASelect from 'ant-design-vue/lib/select'
  import ADivider from 'ant-design-vue/lib/divider'
  import ACheckbox from 'ant-design-vue/lib/checkbox'
  import AInput from 'ant-design-vue/lib/input'
  import ADatePicker from 'ant-design-vue/lib/date-picker'
  import AInputNumber from 'ant-design-vue/lib/input-number'
  import AForm from 'ant-design-vue/lib/form'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import AAlert from 'ant-design-vue/lib/alert'
  import {
    mapMutations
  } from "vuex";
  import {
    mixin,
  } from '@/utils/mixin.js'
  const ASelectOption = ASelect.Option;
  const AFormItem = AForm.Item;
  export default {
    props: {
      selectedRowId: Array,
      customerNum: Number
    },
    components: {
      AModal,
      ASelect,
      ASelectOption,
      ADivider,
      AInputNumber,
      ACheckbox,
      AInput,
      ADatePicker,
      AForm,
      AFormItem,
      ARow,
      ACol,
      AAlert
    },
    mixins: [mixin],
    data() {
      return {
        maskClosable: false, //点击蒙层不关闭modal
        // modify_count:0, //筛选条件条数
        // modify_all_record:true,//是否选择全部
        listNumber: 0,
        size: "small",
        ModalText: "Content of the modal",
        visible: false,
        confirmLoading: false,
        disabledPart: true, //部分条件输入框
        disabledBank: true, //贷款银行
        disabledExpire: true, //到期日期
        diabaledAppoint: true, //预约时间
        disabledType: true, //类型
        disabledCompany: true, //公司
        disabledStatus: true, //状态
        disabledReport: true, //销售说明
        disabledUserAttach: true, //归属于
        disabledBatch: true, //批次
        validParamsList: ['operation', 'limit', 'all_record', 'id_list', 'where'], //有效参数列表，默认值为5个条件字段
        currentOpera: 'select',
        currentLimit: 0,
        currentRecord: true,
        limitMax: 0,
      };
    },
    beforeCreate() {
      this.form = this.$form.createForm(this)
    },
    computed: {
      list_Num() {
        if (this.currentOpera === "select") {
          this.limitMax = this.$props.selectedRowId.length
          return this.currentRecord ? this.limitMax : this.currentLimit
        } else if (this.currentOpera === "search") {
          this.limitMax = this.$props.customerNum
          return this.currentRecord ? this.limitMax : this.currentLimit
        } else {
          this.limitMax = 0
          return 0
        }
      }
    },
    methods: {
      ...mapMutations({
        setCondition: "CONDITION"
      }),
      showBatchModifyModal() {
        this.visible = true;
      },
      handleOk(e) {
        e.preventDefault();
        let _this = this
        this.confirmLoading = true;
        this.form.validateFields((err, values) => {
          // let result="综合信息："+_this.data+",投保需求："+this.getSelectedInsuredPrj();
          if (!err) {
            values.preset_time = values.preset_time ? values.preset_time.format("YYYY-MM-DD HH:mm:ss") : ""
            values.business_expiration_date = values.business_expiration_date ? values.business_expiration_date.format("YYYY-MM-DD HH:mm:ss") : ""
            if (values.operation === "select") {
              values.id_list = (_this.$props.selectedRowId).join(",")
              _this.listNumber = values.id_list.length
            } else if (values.operation === "search") {
              values.where = _this.condition.where
              _this.listNumber = _this.$props.customerNum
            } 
            else if (values.operation === "all") {
  
            } else {
              _this.confirmLoading = false;
              _this.$notification['warning']({
                message: '筛选条件有误',
              })
              return
            }
            for (let item in values) {
              // for(let i;i<this.validParamsList.length;i++){
              // if (!values[item] || !_this.validParamsList.includes(item)) {
              if (!_this.validParamsList.includes(item)) {
                delete values[item]
              }
              // }
            }
            values.all_record = values.all_record ? 'yes' : 'no'
            _this.sendEditDataToServer(values)
          } else {
            _this.$notification['warning']({
                  message: '表单错误',
                })
          }
          _this.confirmLoading = false;
        });
      },
      handleCancel(e) {
        this.visible = false;
      },
      handleChange(value) {
        this.currentOpera = value
      },
      sendEditDataToServer(data) {
        let _this = this
        this.$api.batchUpdate(data).then((result) => {
            _this.$notification['success']({
                  message: '批量修改成功',
                })
            _this.visible = false;
            _this.$emit('refreshCustInfo')
          })
          .catch((err) => {
            _this.$myNotification.sendFailedNotification(err)
          });
      },
      inputNumberChange(value) {
        this.currentLimit = value
      },
      onAllCheckBoxChange(e) {
        const checked = e.target.checked
        if (e.target.checked) {
          this.currentRecord = checked
          this.form.setFieldsValue({
            limit: 0
          }) //将条数归零
          this.currentLimit = 0
        } else {
          this.currentRecord = checked
        }
        this.disabledPart = !this.disabledPart;
      },
      //      贷款银行
      onBankCheckBoxChange() {
        this.disabledBank = !this.disabledBank;
        if (!this.disabledBank) {
          this.validParamsList.push('lending_bank')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'lending_bank')
        }
      },
      //      保险到期日期
      onExpireCheckBoxChange() {
        this.disabledExpire = !this.disabledExpire;
        if (!this.disabledExpire) {
          this.validParamsList.push('business_expiration_date')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'business_expiration_date')
        }
      },
      //      预约时间
      onAppointCheckBoxChange() {
        this.diabaledAppoint = !this.diabaledAppoint;
        if (!this.diabaledAppoint) {
          this.validParamsList.push('preset_time')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'preset_time')
        }
      },
      //      类型
      onTypeCheckBoxChange() {
        this.disabledType = !this.disabledType;
        if (!this.disabledType) {
          this.validParamsList.push('type')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'type')
        }
      },
      handleTypeChange(value) {},
      //      公司
      onCompanyCheckBoxChange() {
        this.disabledCompany = !this.disabledCompany;
        if (!this.disabledCompany) {
          this.validParamsList.push('company')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'company')
        }
      },
      handleCompanyChange(value) {},
      handleOriginChange(value) {},
      //      状态
      onStatusCheckBoxChange() {
        this.disabledStatus = !this.disabledStatus;
        if (!this.disabledStatus) {
          this.validParamsList.push('status')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'status')
        }
      },
      handleStatusChange(value) {},
      //      销售说明
      onReportCheckBoxChange() {
        this.disabledReport = !this.disabledReport;
        if (!this.disabledReport) {
          this.validParamsList.push('report')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'report')
        }
      },
      handleReportChange(value) {},
      //      归属于
      onUserAttachCheckBoxChange() {
        this.disabledUserAttach = !this.disabledUserAttach;
        if (!this.disabledUserAttach) {
          this.validParamsList.push('user_attach_share_type')
          this.validParamsList.push('user_attach_users')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'user_attach_share_type' && item !== 'user_attach_users')
        }
      },
      //      批次
      onBatchCheckBoxChange() {
        this.disabledBatch = !this.disabledBatch;
        if (!this.disabledBatch) {
          this.validParamsList.push('batch')
        } else {
          this.validParamsList = this.validParamsList.filter(item => item !== 'batch')
        }
      },
      handleUserAttachChange(value) {},
      handleUserAttachUserChange(value) {},
      handleBatchChange(value) {
  
      }
    }
  };
</script>

<style lang="less" scoped>
  .ant-input-number {
    margin-right: 5px;
    border-radius: 4px
  }
  
  .modifyItem {
    display: flex;
  }
  
  .modifyItem .ant-checkbox-wrapper {
    margin-top: 5px;
  }
  
  .modifyItem>span {
    width: 120px;
    line-height: 32px;
    text-align: right;
  }
  
  .modifyItem>div.ant-form-item {
    flex: 1;
    margin-bottom: 0;
  }
  
  .condition_container {
    display: flex
  }
  
  .condition_container>div.ant-form-item {
    margin-bottom: 0;
  }
  
  .condition_container>span {
    line-height: 39px
  }
</style>

<style>
  .modifyContent .ant-form-item-children {
    display: flex;
  }
  
  .modifyContent .ant-form-item-label {
    width: 90px;
  }
  
  .form-item-child-one>div:nth-child(2) {
    width: 100px;
  }
</style>




