<template>
  <div class="selectImportFields">
    <a-table :dataSource="getDataSource" @change="handleTableChange" :scroll="{ x: 1300 }" :pagination="false">
      <!--<template slot="name" slot-scope="text, record">-->
      <!--<EditableCell :text="text" @change="onCellChange(record.key, 'name')"/>-->
      <!--
</template>-->
      <a-column-group v-for="(item,index) in columns" :key="index">
        <span slot="title" style="color: #1890ff">
          <a-checkbox />
        </span>
        <a-column :title="item.title" :dataIndex="item.dataIndex" :key="item.key"></a-column>
      </a-column-group>

    </a-table>
  </div>
</template>

<script>
  import ATable from 'ant-design-vue/lib/table'
  import ACheckbox from 'ant-design-vue/lib/checkbox'
  const {
    ColumnGroup,
    Column
  } = ATable;
  const AColumnGroup = ColumnGroup
  const AColumn = Column
  export default {
    props: {
      getDataSource: Array
    },
    components: {
      ATable,
      AColumnGroup,
      AColumn,
      ACheckbox,
    },
    data() {
      return {
        pagination: {
          showQuickJumper: true,
          defaultPageSize: 5
        }
      };
    },
    computed: {
      columns() {
        let {
          sortedInfo,
          filteredInfo
        } = this;
        sortedInfo = sortedInfo || {};
        filteredInfo = filteredInfo || {};
        let columnsTemp = [];
        let columns = [{
            title: "姓名",
            dataIndex: "owner",
            width: 100,
            key: "owner",
            fixed: "left",
            isEmpty: false
          },
          {
            title: "手机",
            dataIndex: "mobile",
            key: "mobile",
            isEmpty: false
          },
          {
            title: "车牌",
            dataIndex: "plate_no",
            key: "plate_no",
            isEmpty: false
          },
          {
            title: "品牌",
            dataIndex: "team",
            key: "team",
            isEmpty: true
          },
          {
            title: "注册日期",
            dataIndex: "register_date",
            key: "register_date",
            isEmpty: false
          },
          {
            title: "保险公司",
            dataIndex: "company",
            key: "company",
            isEmpty: false
          },
          {
            title: "商业到期",
            dataIndex: "business_expiration_date",
            key: "business_expiration_date",
            isEmpty: false
          },
          {
            title: "预约时间",
            dataIndex: "preset_time",
            key: "preset_time",
            isEmpty: false
          },
          {
            title: "状态",
            dataIndex: "status",
            key: "status",
            isEmpty: false
          },
          {
            title: "批次",
            dataIndex: "batch",
            key: "batch",
            isEmpty: false
          },
          {
            title: "类型",
            dataIndex: "type",
            key: "type",
            isEmpty: false
          },
          {
            title: "备注",
            dataIndex: "remark",
            key: "remark",
            isEmpty: false
          },
          {
            title: "归属于",
            dataIndex: "user_attach",
            key: "user_attach",
            isEmpty: false
          },
          {
            title: "操作",
            key: "operation",
            dataIndex: "operation",
            fixed: "right",
            width: 100,
            scopedSlots: {
              customRender: "operation"
            }
          }
        ];
        //将值为空的列放到后面
        columnsTemp = columns.filter(item => item.isEmpty == false);
        columns.forEach(item => {
          if (item.isEmpty) {
            columnsTemp.push(item);
          }
        });
        return columnsTemp;
      }
    }
  };
</script>

<style>
  .selectImportFields table thead tr th {
    text-align: center;
  }
  
  .selectImportFields table thead tr:first-child th {
    padding-top: 16px;
    padding-bottom: 0;
  }
</style>




